* {
  box-sizing: border-box;
}

.form_section {
  padding: 60px 0;
  min-height: 100vh;
  background-color: var(--bcg-color-blue);
  position: relative;
  overflow: hidden;
}

.div_container {
  padding: 0 20px;
  width: 100%;
  margin: 0 auto;
}
.div_container_formik {
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
  border-radius: 8px;
  background: var(--bcg-color-white);
  padding: 40px;
}

.logo_goose {
  display: none;
  position: absolute;
  width: 400px;
  bottom: 0px;
  left: 0;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
  gap: 10px;
}

.logoImage {
  width: 71px;
}

.logoText {
  color: var(--main-color-blue);
  font-family: Inter, sans-serif;
  font-size: 24px;
  font-weight: 700;
}

.logoTextItalic {
  font-style: italic;
}

@media screen and (min-width: 1440px) {
  .logo_goose {
    display: block;
  }
}
.title {
  color: var(--main-color-blue);
  text-shadow: 0px 9.399999618530273px 57.6875px rgba(0, 0, 0, 0.04),
    0px 47px 355px rgba(0, 0, 0, 0.07);
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  text-align: left;
  margin-bottom: 20px;
}
@media screen and (min-width: 768px) {
  .title {
    font-size: 24px;
  }
}
.form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.form_div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
}
.form_label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  width: 100%;
  color: var(--text-main-text);
  font-size: 12px;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 18px;
}
@media screen and (min-width: 768px) {
  .form_label {
    font-size: 14px;
  }
}

.form_label_tw {
  width: 100%;
  color: var(--text-main-text);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.4;
}
@media screen and (min-width: 768px) {
  .form_label_tw {
    font-size: 14px;
    font-weight: 600;
  }
}

.form_input_div {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
/* .form_input_email {
margin-bottom: 8px;
} */
.form_input {
  position: relative;
  font-family: inherit;
  width: 100%;
  padding: 14px 0px 14px 14px;
  color: var(--text-main-text);
  font-weight: 400;
  line-height: 18px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  border-radius: 8px;
  border: 1px solid rgba(220, 227, 229, 0.6);
  background: var(--bcg-color-white);
  font-size: 12px;
}

@media screen and (min-width: 768px) {
  .form_input {
    padding: 18px 18px;
    font-size: 16px;
  }
}

.button {
  margin-top: 35px;
  display: flex;
  width: 100%;
  padding: 16px;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  background: var(--main-color-blue);
  box-shadow: rgba(136, 165, 191, 0.48) 4px 2px 16px;
  color: var(--bcg-color-white);
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  letter-spacing: -0.28px;
  border: none;
  cursor: pointer;
  font-family: Inter;
}
@media screen and (min-width: 768px) {
  .button {
    font-size: 18px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
  .button {
    line-height: 24px;
    letter-spacing: -0.36px;
  }
}

.google {
  display: flex;
  width: 100%;
  max-width: 100%;
  padding: 16px 0;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  border: 1px solid rgb(220, 227, 229);
  background: var(--bcg-color-white);
  box-shadow: rgba(136, 165, 191, 0.48) 4px 2px 16px;
  color: var(--text-main-text);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: -0.28px;
}
@media screen and (min-width: 375px) {
  .google {
    font-size: 14px;
  }
}
@media screen and (min-width: 768px) {
  .google {
    font-size: 18px;
  }
}
.register {
  margin-top: 18px;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .register {
    margin-top: 24px;
  }
}
